<template>
    <div id="Set">
        <van-nav-bar
            title=""
            left-arrow
            @click-left="onClickLeft"
            />
        <van-cell @click="toUpdateUpwd" title="" is-link />
        <!--  -->
        <div id="btn">
            <div v-if="!uid">  
                <van-button @click="toLogin" id="login" plain type="primary"></van-button>
             </div>
             <div v-else>  
              <van-button id="loginout" @click="loginout" plain type="primary"></van-button>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState,mapMutations  } from "vuex"
export default {
    //vuex
    computed:{
        ...mapState([
            "uid"
        ])
    },
    data(){
        return{

        }
    },
    methods:{
           ...mapMutations([
            "setUid"
        ]),
        //
        toUpdateUpwd(){
            this.$router.push("/accountinfo")
        },
        //
        onClickLeft(){
            this.$router.push("/person")
        },
        //
        toLogin(){
            this.$router.push("/login")
        },
        //
        loginout(){
            // localStroage
            localStorage.clear();
            //vuex
            this.setUid("")
            this.$router.push("/person")

        }
    }
}
</script>
<style>
/*  */
  #Set .van-icon-arrow-left{
      font-size:20px !important;
      color:#743481;

  }
  #Set .van-cell--clickable{
        width:100% !important;
        height:50px;
    }
 #btn>div{
     display:flex;
     justify-content:center;
 }
   .van-button--plain{
       display:block;
        width:80%;
        /* margin:0 auto !important; */
        margin-top:50px !important;
        border:1px solid #743481 !important;
        color:#743481 !important;
   }
   #loginout .van-button__text{
       color:red !important;
   }

</style>