<template>
    <div id="FeedBack">
        <!--  -->
        <div id="Feedback">
            <div @click="back" class="iconfont iconfanhui"></div>
            <div></div>
            <div  @click="Feedback"></div>
        </div>
        <!--  -->
        <p class="hr"></p>
        <!--  -->
        <textarea v-myfocus v-model="val" id="question" placeholder=""></textarea>
    </div>
</template>
<script>
//
import { Feedback } from "../assets/js/apis/user"
import { mapState } from "vuex"
export default {
    computed:{
        ...mapState([
            "uid"
        ])
    },
    data(){
        return{
            val:""
        }
    },
    methods:{
        Feedback(){
            //
            if(!this.val.trim()){
                this.$toast("")
            }
            //
            FeedBack(this.uid,this.val).then(res=>{
                console.log(res)
            })
        },
        //
        back(){
            history.go(-1)
        }
    }
}
</script>
<style>
    #Feedback{
        display:flex;
        width:100%;
        height:60px;
        justify-content:space-around;
        align-items:center;
        background-color:white;
    }
    #Feedback>div:nth-child(2){
        font-size:20px;
    }
    #Feedback .iconfanhui{
        font-size:24px;
        transform:translate(-100%);
    }
     #FeedBack .hr{
        height:10px;
        background-color:rgb(223, 219, 219);
    }
    #question{
        width:100%;
        min-height:200px;
        max-height:200px;
        border:0;
        margin-top:10px;
        background-color:rgb(204, 202, 202);
        padding:5px 0;
    }

</style>