<template>
<!--  -->
    <div id="excerpt">
        <div id="excerpt-head">
            <div @click="back" class="iconfont iconfanhui"></div>
            <div></div>
        </div>
        <!--  -->
        <div id="Details">
            <!--  -->
            <div id="excerpt-header">
                <div>
                    <div><img :src="`${url}/avatar${bookdata.avatar}`" alt=""></div>
                    <div id="nickname" v-text="bookdata.nickname"></div>
                </div>
                <div v-text="attentionmsg" v-show="uid!=bookdata.uid"  @click="attention(bookdata.uid)"></div>
            </div>
            <!--  -->
            <div v-text="bookdata.viewcontent" id="excerpt-padding"> </div>
            <!--  -->
            <!---->
            <p v-show="bookdata.imgs" id="images">
                <img v-for="itemImage,i of bookdata.imgs" :src="`${url}/usersimage/${itemImage}`">
            </p>
            <!--  -->
            <!--  -->
            <div @click="toDetails(bookdata.bid)" id="excerpt-book">
                <img :src="`${url}/coverimage/${bookdata.coverimage}`" alt="">
                <div>
                    <h1 v-text="bookdata.bookname"></h1>
                    <h2 v-text="bookdata.author"></h2>
                </div>
            </div>
            <!--  -->
            <h6></h6>
            <!--  -->
            <div id="excerpt-title">
                <div></div>
                <div>{{num}}</div>
            </div>
            <div v-if="num">
                <div v-for="item,i of info" :key="i" class="excerpt-users">
                        <div><img :src="`${url}/avatar/${item.avatar}`"></div>
                        <div class="excerpt-info">
                            <h2 v-text="item.nickname"></h2>
                            <p v-text="item.details"></p>
                            <i>{{item.date|mydate}}</i>
                        </div>
                </div>
            </div>
            <div v-else id="excerpt-err">
                <img :src="`${url}/err.jpg`" alt="">
            </div>
    </div>
      <!--  -->
        <div id="footerView">
            <input v-model="val" type="text" placeholder=",">
            <div @click="send" class="iconfont iconfasong"></div>
        </div>
</div>
</template>
<script>
//
import { BookView,bookviewdetails } from '../assets/js/apis/book'
import { Attention,isAttention,Send } from "../assets/js/apis/user"
import { baseURL } from '../assets/js/apis/url'
import { mapState } from "vuex"
export default {
    computed:{
        ...mapState([
            "uid"
        ])
    },
    data(){
        return{
            //
            url:baseURL,
            bookdata:{avatar:"/default.png",coverimage:"bainiangudu.jpg"},
            //
            num:0,
            //
            info:[],
            //
            attentionmsg:"",
            //
            val:""
        }
    },
    methods:{
        back(){//
            history.go(-1)
        },
        //axios
        attention(id){
            if(!this.uid){
                // 
                this.$router.push("/login")
                return;
            }
            //       id  id
            Attention(id,this.uid).then(res=>{
                //0
                //1
                if(res==1){
                    this.attentionmsg=""
                }else{
                    this.attentionmsg=""
                }

            })
        },
        //
        toDetails(id){
            this.$router.push("/bookdetail/"+id)
        },
        //
        send(){
            if(!this.uid){
                this.$router.push("/login")
                return;
            }
            //
            if(!this.val.trim()){
                this.$toast("")
                return;
            }
           Send(this.bookdata.vid,this.val,this.uid).then(res=>{
               if(res==1){
                   // 
                       bookviewdetails(this.$route.params.vid).then(res=>{
                            if(res!=0){//0info
                                this.info=res;
                                this.num=res.length//num
                            }else{
                                this.num=res
                            }
                        })
               }
           })
        }
    },
    created(){
        //
        BookView(this.$route.params.vid).then(res=>{
            // console.log(res)
            // return
            //
                    if(res.imgs){
                        res.imgs=res.imgs.split(",")
                    }
                    this.bookdata=res
                  //  console.log(this.bookdata)
                  //
                isAttention(this.bookdata.uid,this.uid).then(res=>{
                    //1
                    //0
                   // console.log(res)
                        this.attentionmsg=res.msg
                
                })
        })
        //
        bookviewdetails(this.$route.params.vid).then(res=>{
            if(res!=0){//0info
                this.info=res;
                this.num=res.length//num
            }else{
                this.num=res
            }
        })

    },
  
}
</script>
<style>
    /*  */
    #excerpt #excerpt-head{
        display:flex;
        width:100%;
        height:60px;
        position:fixed;
        top:0;
        align-items:center;
        background-color:white;
    }
    #excerpt #excerpt-head .iconfanhui{
        font-size:24px;
        margin-left:10px;
    }
    #excerpt #excerpt-head>div:last-child{
        position:absolute;
        left:calc(50% - 50px);
        font-size:20px;
    }
 #Details{
       width:90%;
       margin:60px auto;

 }
 /*  */
    #footerView{
        width:100%;
        height:60px;
        /* margin:0 auto; */
        background-color:white;
        display:flex;
        position:fixed;
        bottom:0;
        justify-content:space-around;
        align-items:center;
        border-top:1px solid black;
    }
    #footerView>input{
        width:80%;
        height:40px;
        border-radius:25px;
        padding-left:15px;
        font-size:14px;
        background-color:rgb(197, 197, 197);
    }
    #footerView>input::placeholder{
        color: gray;
    }
    #footerView .iconfasong{
        font-size:24px;
    }
    /*  */
    #excerpt-header{
        display:flex;
        justify-content:space-between;
        align-items:center;
    }
    #excerpt-header>div:first-child{
        display:flex;
        align-items:center;
    }
    #excerpt-header>div:first-child #nickname{
        margin-left:10px;
    }
      #excerpt-header>div:last-child{
        width:60px;
        text-align:center;
        padding:5px 0;
        background-color:#743481;
        color:white;
        border-radius:20px;
    }
    #excerpt-header img{
        width:35px;
        height:35px;
        border-radius:50%;
    }
    /*  */
     #excerpt-padding{
         /* width:100%; */
         height:300px;
         background:#c46ad6;
         margin:10px 0;
         padding:20px 20px;
         border-radius:5px;
         color:white;
         letter-spacing:3px;
         font-size:20px;
         font-family:"";
     }
    /*  */
    #excerpt-book{
        display:flex;
        /* width:100%; */
        background-color:rgb(230, 230, 230);
        padding:10px 0 10px 5px;
        border-radius:5PX;
    }
     #excerpt-book img{
         width:40px;
     }
     #excerpt-book>div{
         margin-left:10px;
     }
     #excerpt-book>div>h2{
         margin-top:10px;
     }
     /*  */
     #excerpt-title{
         display:flex;
         justify-content:space-between;
         align-items:center;
         margin:15px 0;
         color:gray;
     }

     /*  */
      /*  */
    #excerpt-err>img{
        width:100%;
    }
    .excerpt{
        display:flex;
        margin-top:15px;
    }
    .excerpt-users{
        display:flex;
        margin-top:15px;
    }
    .excerpt-users img{
        width:30px;
        height:30px;
        border-radius:50%;
    }
    .excerpt-info{
        margin-left:10px;
        border-bottom:1px solid gray;
        padding-bottom:3px;
        width:80%;
    }
    .excerpt-info>h2{
        color:#743481;
        font-size:14px;
        margin-top:5px;
    }
    .excerpt-info>p{
        font-size:14px;
        line-height:20px;
        margin-top:10px;
    }
    .excerpt-info i{
        display: inline-block;
        margin-top:10px;
        color:gray;   
    }
    #images::after{
        content:"";
        display:block;
        clear:both;
    }
    /*  */
    #images img{
        width:30%;
        /* float:left; */
        margin:0 5px;
    }
</style>