<template>
<!--  -->
    <div id="BookDetail">
          <!--  -->
          <div class="header">
              <!--  -->
              <div @click="toredirect" class="iconfont iconfanhui"></div>
              <!--  -->
              <div class="bookname" v-text="bookdata.bookname"></div>
          </div>
          <div id="Content">
          <!--  -->
          <div id="Bg">
            <div id="details">
                    <img :src="`${url}/coverimage/${bookdata.coverimage}`" alt="">
                    <div>
                        <p v-text="bookdata.bookname"></p>
                        <h1 v-text="bookdata.author"></h1>
                    </div>
            </div>
          </div>
          <!--  -->
          <div id="description">   
            <h2></h2>
            <p v-text="bookdata.intro"></p>
         </div>
         <!--  -->
         <p id="hr"></p>
         <!--  -->
         <div id="review">
             <div id="book">
                 <h2></h2>
                 <h5 v-show="reviewLength>0">{{reviewLength}}</h5>
             </div>
             <h3 @click="toWrite">,...</h3>
             <!--  -->
             <div v-if="reviewLength">
                <div v-for="item,i of review" :key="i" class="users">
                    <div><img :src="`${url}/avatar/${item.avatar}`" alt=""></div>
                    <div class="info">
                        <h2 v-text="item.nickname"></h2>
                        <p v-text="item.review"></p>
                        <i>{{item.date|mydate}}</i>
                    </div>
                </div>
             </div>
             <!--   -->
             <div id="err" v-else>
                 <img :src="`${url}/err.jpg`" alt="">
             </div>
         </div>

            <!--  -->
         <div v-show="review.length==3" id="more">
             <router-link :to="`/allreview/${val}`">>></router-link>
         </div>
         </div>
      
         <!--  -->
         <div id="addBookself">
             <button @click="join"></button>
         </div>
    </div>
</template>
<script>
import { bookDetails,bookreview } from '../assets/js/apis/book'
import { joinShelf } from '../assets/js/apis/user'
import { baseURL } from '../assets/js/apis/url'
import Vue from "vue"
import { mapState } from "vuex"
//
Vue.filter("mydate",(oldVal)=>{
        let date = new Date(oldVal).toLocaleDateString()
        let huors=new Date(oldVal).getHours()
        huors= huors<10 ? "0"+ huors : huors
        let mins = new Date(oldVal).getMinutes()
        mins= mins<10 ? "0"+mins : mins
        let time=huors+":"+mins
        return date + " "+time
})
export default {
    computed:{
        ...mapState([
            "uid"
        ])
    },
    data(){
        return{
            //id
            val:"",
            // 
            bookdata:{coverimage:"guaidanxingweixue.jpg"},
            //url
            url:baseURL,
            //
            review:[],
            //
           reviewLength:""
        }
    },
    methods:{
        toredirect(){
            //
            history.go(-1)
        },
        //
        toWrite(){
           if(!this.uid){//,
                this.$router.push("/login")
                return;
            }
            this.$router.push(`/write/${this.val}`)
        },
        //
        join(){
            if(!this.uid){//,
                this.$router.push("/login")
                return;
            }
            joinShelf(this.uid,this.val).then(res=>{
                //0
                if(res==1){
                    this.$toast("")
                }else{
                    this.$toast(",")
                }
                //1
            })
        }
    },
    created(){
        this.val=this.$route.params.bid
        //axios
        bookDetails(this.val).then(res=>{
            this.bookdata=res
        })
        //axios
        bookreview(this.val).then(res=>{
            this.reviewLength=res.length
            if(res.length>3){//3
                let data = []
                for(var i=0;i<3;i++){
                    data.push(res[i])
                }
                this.review=data;
            }else{
                this.review=res
            }
        })
   
    }
}
</script>
<style>
   #BookDetail .header{
       display:flex;
       align-items:center;
       width:100%;
       height:60px;
       position:fixed;
       top:0;
       background-color:white;
   }
   #BookDetail .iconfanhui{
       font-size:24px;
       margin-left:10px;
       /* width:50%; */
   }
 #BookDetail .bookname{
     /* width:50%; */
     position:absolute;
     left:50%;
     transform:translate(-50%);
     font-size:20px;
     letter-spacing:2px;
     /* text-align:left; */
 }

 /*   */
 #Bg{
     background-color:#743481;
 }
 #details{
     display:flex;
     width:90%;
     margin:0 auto;
     padding:10px 0;
     color:white;
 }
 #details img{
     width:25%;
 }
 #details>div:nth-child(2){
     margin-left:20px;
 }
 #details p{
     font-size:20px;
     margin-top:10px;
 }
 #details h1{
     margin-top:20px;
 }
 /*  */
 #description{
     width:95%;
     margin:0 auto;
     margin-top:20px;
     
 }
 #description>h2{
     font:24px "";
     font-weight:bold;
 }
 #description>p{
     font:12px "";
     margin-top:10px;
     color:gray;

 }
 /*  */
 #hr{
     height:10px;
     background-color:#743481;
     margin-top:5px;
 }
 /*  */
    #review{
        width:95%;
        margin:0 auto;
        margin-top:20px;
    }
    #review>h3{
        width:75%;
        margin:10px auto;
        height:30px;
        border-radius:15px;
        text-align:center;
        line-height:30px;
        color:#743481;
        background:rgb(218, 218, 218);
    }
    #review #book>h2{
         font:24px "";
        font-weight:bold;
    }
    #book{
        display:flex;
        justify-content:space-between;
        align-items:center;
    }
    #err>img{
        width:100%;
    }
    .users{
        display:flex;
        margin-top:15px;
    }
    .users img{
        width:30px;
        height:30px;
        border-radius:50%;
    }
    .info{
        margin-left:10px;
        border-bottom:1px solid gray;
        padding-bottom:3px;
        width:80%;
    }
    .info>h2{
        color:#743481;
        font-size:14px;
    }
    .info>h2{
        margin-top:5px;
    }
    .info>p{
        font-size:14px;
        line-height:20px;
        margin-top:10px;
    }
    .info i{
        display: inline-block;
        margin-top:10px;
        color:gray;   
    }
    /*  */
    #addBookself{
        position:fixed;
        bottom:0;
        width:100%;
        height:60px;
        background:white;
        display:flex;
        justify-content:center;
        align-items:center;
        border-top:.5px solid black;
    }
    #addBookself button{
        outline:0;
        border:0;
        width:80%;
        height:50px;
        background-color:#743481;
        color:white;
        border-radius:5px;
        letter-spacing:2px;
    }
    /*  */
    #Content{
        margin:60px 0;
        min-height:600px;
    }
    /*  */
    #more{
        text-align:center;
        padding:20px 0;
    }
    #more>a{
        color:#743481;
    }
</style>