<template>
    <div id="Login">
       <van-nav-bar class="login" title="" left-arrow @click-left="onClickLeft">
        </van-nav-bar>
        <!-- logo -->
        <div class="login-logo">
            <img src="/imgs/booklogo.png" alt="">
        </div>
        <!--  -->
        <div id="form">
        <input class="phoneStyle" type="text" v-model="phone" placeholder="">
        <div id="upwd">
            <input class="upwdStyle" :type="isShow" v-model="upwd" placeholder="">
            <div @click="showOrHide" class="iconfont" :class="eye"></div>
        </div>
     </div>
    <p class="forget"> 
        <router-link to="">?</router-link>
    </p>
    <!--  -->
    <div class="btn">
        <van-button @click="Login" plain type="primary"></van-button>
        <van-button @click="toReg" type="primary"></van-button>
    </div>
      <!-- -->
      <div>
          <div id="threeFont">----</div>
          <div id="threeLogin">
            <router-link to="" class="iconfont iconweixin"></router-link>
            <router-link to="" class="iconfont iconqq"></router-link>
            <router-link to="" class="iconfont icon89"></router-link>
          </div>
      </div>
 </div>
  
</template>
<script>
//login 
import { login } from "../assets/js/apis/user.js" 
import { mapMutations } from "vuex"
export default {
    data(){
        return{
            //
            phone:"",
            //
            upwd:'',
            //
            phoneState:"",
            //
            upwdState:"",
            //
             eye:"iconyincang",
             // 
            isShow:"password",

        }
    },
    methods:{
     ...mapMutations([
            "setUid"
        ]),
        //
         onClickLeft(){
            this.$router.push("/person")
        },
            //  "" 
        showOrHide(){
            if(this.isShow=="password"){//
                this.eye="iconai-eye";//
                this.isShow="text";//
            }else{//
                this.eye="iconyincang";//
                this.isShow="password";//
            }
        },
        //
       Login(){
           if(!this.phone.trim() || !this.upwd.trim()){//
                this.$toast({
                    message:"",
                    position:"top",
                })
                return
           };
           //
           login(this.phone,this.upwd).then(res=>{
               if(res!=0){
                      this.$toast({
                        message:"",
                        position:"top",
                    })
                    localStorage.setItem("uid",res.uid)
                    this.setUid(res.uid)
                    //
                    let url = this.$route.query
                    if(url.redirect){//
                        this.$router.push(url.redirect)
                    }else{//
                        this.$router.push("/person")
                    }
                    
               }else{
                   this.$toast({
                        message:"",
                        position:"top",
                    })
               }
           })
         
       },
        //
        toReg(){
            this.$router.push("/reg")
        }
    }
}
</script>
<style>
/*  */
#Login .van-icon-arrow-left{
   font-size:24px !important;
   color:#743481;
}
/*  */
 #Login .van-nav-bar__title{
    font-size:24px !important;
}
 #Login .login-logo>img{
     display:block;
   width:200px;
   height:200px;
   margin:0 auto;
 }
/*  */
    #Login .nav{
        background-color:white;
    }

/*  */
   #Login .phoneStyle{
       height:30px;
         display:block;
       width:70%;
       border-bottom:1px solid #743481;
       margin:0 auto;
        margin-top:20px;
        padding:5px 0;
   }
  /*  */
  #Login #upwd{
        width:70%;
        margin:0 auto;
        height:30px;
        position:relative;
   }
  #Login #upwd>.upwdStyle{
        width:100%;
        display:block;
        border-bottom:1px solid #743481;
        margin-top:20px;
        padding:5px 0;
   }
   #Login #upwd>div{
        position:absolute;
        top:0px;
        right:0px;
    }
   /*  */
   #Login .forget{
       width:70%;
       margin:0 auto;
       margin-top:20px;
       text-align:right;
   }
   #Login .forget a{
      color:gray;
      position: relative;
     text-decoration:none;
   }
   /*  */
   #Login .btn{
       width:70%;
       margin:0 auto;
       margin-top:-35px;
   }
   #Login .btn button{
       display: block;
        width:100%;
   }
   #Login .btn button:first-child{
       outline:0;
       border:1px solid #743481;
       color: #743481;
   }
   #Login .btn button:last-child{
       outline:0;
       border:0;
       margin-top:10px;
       background-color:#743481;
   }
   /*  */
   #Login #threeFont{
       text-align:center;
       color:gray;
       margin-top:30px;
       margin-bottom:10px;
       letter-spacing:2px;
   }
   #Login #threeLogin{
       display:flex;
       justify-content:center;
   }
   #Login #threeLogin>a{
      font-size:32px;
   }
   #Login #threeLogin>a:nth-child(2){
       margin:0 30px;
   }
</style>