<template>
    <!--  -->
    <div id="setUpwd">
    <!--  -->
        <header id="setUpwd-header">
            <div @click="back" class="iconfont iconfanhui"></div>
            <div></div>
        </header>
        <!--  -->
        <section>
            <div id="oldupwd">
                <!--  -->
                <i class="iconfont iconmima"></i>
                <input v-model="oldUpwd" :type="oldShow" placeholder="">
                <i class="iconfont" :class="oldeye" @click="oldShowClick"></i>
            </div>
            <div id="newupwd">
                <!--  -->
                <i class="iconfont iconmima"></i>
                <input v-model="newUpwd" :type="newShow" placeholder="6~20">
                <i class="iconfont" :class="neweye" @click="newShowClick"></i>
            </div>
            <div id="newcpwd">
                <!--  -->
                <i class="iconfont iconmima"></i>
                <input v-model="newCpwd" :type="newcShow" placeholder="">
                <i class="iconfont" :class="newceye" @click="newcShowClick"></i>
            </div>
        </section>
        <!--  -->
        <button id="update" @click="update"></button>
    </div>
    
</template>
<script>
import {setUpwd} from "../assets/js/apis/user"
import {mapState,mapMutations} from "vuex"
export default {
    computed:{
        ...mapState([
            "uid"
        ])
    },
    data(){
        return{
            //
            oldUpwd:"",
            //
            newUpwd:"",
            //
            newCpwd:"",
            //
            oldShow:"password",
            newShow:"password",
            newcShow:"password",
            //
            oldeye:"iconyincang",
            neweye:"iconyincang",
            newceye:"iconyincang"
        }
    },
    methods:{
        ...mapMutations([
            "setUid"
        ]),
        //
        back(){
            this.$router.go(-1)
        },
        //
        update(){
             //6~20
            let reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/
            if(!reg.test(this.newUpwd)){
                this.$toast("6~20")
                return;
            }
            //
            if(this.newUpwd!=this.newCpwd){
                this.$toast("")
                return
            }
            ///axios
            setUpwd(this.uid,this.oldUpwd,this.newUpwd).then(res=>{
                if(res==1){
                    this.$toast("")
                    //
                    localStorage.clear();
                    this.setUid("")
                    this.$router.push("/login")
                }else{
                    this.$toast("")
                }
            })
           
        },
        // 
        oldShowClick(){
            if(this.oldShow=="password"){
                // ()
                this.oldShow="text"
                this.oldeye="iconai-eye"
            }else{
                // 
                this.oldShow="password"
                this.oldeye="iconyincang"
            }
        },
        //
        newShowClick(){
            if(this.newShow=="password"){
                // ()
                this.newShow="text"
                this.neweye="iconai-eye"
            }else{
                // 
                this.newShow="password"
                this.neweye="iconyincang"
            }
        },
        //
         newcShowClick(){
            if(this.newcShow=="password"){
                // ()
                this.newcShow="text"
                this.newceye="iconai-eye"
            }else{
                // 
                this.newcShow="password"
                this.newceye="iconyincang"
            }
        },
    }
}
</script>
<style>
 #setUpwd-header{
    display:flex;
    align-items:center;
    position:relative;
    height:60px;
    margin-bottom:30px;
 }
 #setUpwd-header .iconfanhui{
     margin-left:20px;
 }
 #setUpwd-header>div:nth-child(2){
     font-size:24px;
     position:absolute;
     left:50%;
     transform:translate(-50%);
 }
 /*  */
 #oldupwd,#newupwd,#newcpwd{
     position:relative;
     width:70%;
     margin:0 auto;
     border-bottom:1px solid #743481;
 }
 #setUpwd input{
      padding:13px 0;
     padding-left:22px;
 }
    
 /*  */
#setUpwd .iconmima{
    position:absolute;
    right:0;
    top:10px;
    font-size:20px;
 }
 /*  */
 #setUpwd .iconmima{
    left:0;
    top:10px;
    width:20px;
    color:#743481;
 }
  #setUpwd .iconyincang,#setUpwd .iconai-eye{
        position:absolute;
        top:12px;
        right:0;
 }
 /*  */
 #setUpwd #update{
     outline:0;
     border:0;
     display:block;
     width:70%;
     height:40px;
     margin:0 auto;
     margin-top:30px;
     color:white;
     border-radius:5px;
     /* cursor:pointer; */
     background-color:#743481;
 }
</style>