<template>
    <div id="Person">
        <!--  -->
        <div class="icon">
            <router-link to="" class="iconfont iconxiaoxi1"></router-link>
            <router-link to="/set" class="iconfont iconshezhi1"></router-link>
        </div>
        <!--  -->
        <div id="avator">
