            </a>
        </div>
        <van-share-sheet
            v-model="showShare"
            title=""
            :options="options"
            @select="share"
            />
          <!-- tabbar -->
     <my-tabbar></my-tabbar>
    </div>
</template>
<script>
//
import { baseURL } from "../assets/js/apis/url.js"
import { person } from "../assets/js/apis/user"
//vuexstate
import { mapState} from "vuex"
export default {
    //vuexuname
    computed:{
        ...mapState([
            "uid",
            "avator"
        ])
    },
    data(){
        return {
            // 
            url:baseURL,
            //
            Person:{avatar:"default.png"},
            //
            showShare:false,
            //
            options: [
                [
                    { name: '', icon: 'wechat' },
                    { name: '', icon: 'weibo' },
                    { name: 'QQ', icon: 'qq' }
                ],
                [
                    { name: '', icon: 'link' },
                    { name: '', icon: 'poster' },
                    { name: '', icon: 'qrcode' }
                ],
            ],
        }
    },
    created(){
       // console.log(this.uid)
        if(this.uid){//uid
                //
                person(this.uid).then(res=>{
                // console.log(res)
                    this.Person=res
                })
        }
        

    }
    ,
    methods:{
       share(option,index){
           console.log(index)
           console.log(option)
       },
        //
        cloudBook(){
            // 
                // 
                this.$router.push("/cloudBook")
        },
        //
        myCreation(){
           this.$router.push("/myCreation")
        },
        //
        myNote(){
            this.$router.push("/myNote")
        },
        //
        myColl(){
            this.$router.push("/myColl")
        },
        //
        friend(){
             this.showShare=true
        },
        //
        feedback(){
            this.$router.push("/feedback")
        },
        //
        update(){
            this.$router.push("/update")
        }
    }
}
</script>>
