<template>
        <div class="Reg">
        
        <!--  -->
        <van-nav-bar class="zhuce" title="" left-arrow @click-left="onClickLeft">
           
        </van-nav-bar>
        <div class="logo">
            <img src="/imgs/booklogo.png" alt="">
        </div>
         <!--  -->
         <div class="Reg-info">
             <!--  -->
             <div id="nickname">
                <van-field v-model="nickname" @blur.native.capture="checkNickUname" placeholder="2~10"/>
             </div>
             <!-- input -->
             <div id="phone">
             <van-field v-model="phone" maxlength="11"  @blur.native.capture="checkphone" placeholder="" />
             </div>
            <!--  -->
            <div id="password">
                <van-field v-model="upwd" @blur.native.capture="checkupwd" :type="isShow" placeholder="6~20"/>
                <div @click="showOrHide" class="iconfont position" :class="eye"></div>
              </div>
           <!--  -->
            <div id="authCode">
                 <van-field v-model="code" @blur.native.capture="codeBulr"  class="Code" placeholder=""/>
                 <canvas id="canvas" @click="canvas"></canvas>
            </div>
        <!--  -->
        </div>
        <!--  -->
        <div id="NextFather">
            <van-button id="Next"  @click="reg" type="primary"></van-button>
        <!-- <mt-button type="primary" @click="reg" size="large"></mt-button> -->
        </div>

        <!-- -->
        <label id="isAgreen" for="">
                <van-checkbox v-model="checked"></van-checkbox>
                
                <router-link to="/Priv"></router-link>
        </label>

       
    </div>
</template>
<script>
// 
//        
import { reg,code,Phone } from '../assets/js/apis/user'
//
import { mapMutations } from "vuex"
//vue 
import Vue from 'vue'
Vue.directive("myfocus",{
    inserted(elem){
        elem.focus()
    }
})
export default {
    data(){
        return{
            // 
            nickname:"",
            // 
            code:"",
            //
            isCode:"",
            //
            phone:"",
            //
            upwd:'',
            // 
            isShow:"password",
            // 
            eye:"iconyincang",
            //
            checked:false,
            isPhone:true

        }
    },
    mounted(){//
        this.canvas()
    },
    methods:{
        //
        ...mapMutations([
            "setUid",
        ]),
        // 
        onClickLeft(){
            this.$router.push("/login")
        },
        //canvas
        canvas(){
            code().then(res=>{
                //console.log(res)
                //canvas
                let canvas=document.getElementById("canvas")
                //
                canvas.width=80
                canvas.height=30
                // 
                let ctx=canvas.getContext('2d');
                this.isCode=res
                // /
                ctx.font="20px "
                //
                ctx.fillStyle="#f00"
                //
                ctx.fillText(this.isCode,20,20)
            })
            
        },
        //
        checkNickUname(){
             //
             let reg = /^\w{2,10}$/
            if(!reg.test(this.nickname)){//
                this.$toast({
                    message:"",
                    position:"top",
                })
                return false;
            }else{
                return true;
            }
        },
        //  "" 
        showOrHide(){
            if(this.isShow=="password"){//
                this.eye="iconai-eye";//
                this.isShow="text";//
            }else{//
                this.eye="iconyincang";//
                this.isShow="password";//
            }
        },
        //  
  
        //
        checkphone(){
            //
           
            //
            let reg=/^1[3-9]\d{9}$/;
            if(reg.test(this.phone)){
                Phone(this.phone).then(res=>{
                    if(res==1){//
                        this.$toast({
                            message:"",
                            position:"top",
                        })
                        this.isPhone=false;
                    }
                   // console.log(this.phone)
                        this.isPhone =true;
              }) 
               
            }else{
                this.$toast({
                    message:"",
                    position:"top",
                })
                return false;
            }
        },
        //
        checkupwd(){
            //
            //
            let upwdReg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/;
            if(upwdReg.test( this.upwd)){
                this.Upwd="success";
                return true;
            }else{
                this.Upwd="error";
                this.$toast({
                    message:"",
                    position:"top",
                });
                return false;
            }
        },
        // 
        codeBulr(){
            if(!this.code.trim()){//
                 this.$toast({
                    message:"",
                    position:"top",
                 });
                 return false;
            }else{
                return true;
            }
        },
        //
        reg(){
            //
            if(!this.checked){
                this.$toast({
                    message:"",
                    position:"top",
                 });
                 return;
            };
             //
             if(this.code!=this.isCode){
                 this.$toast({
                    message:"",
                    position:"top",
                 });
                 return;
             };
            // console.log(this.checkphone())
             //            

            if(this.checkNickUname() && this.isPhone && this.checkupwd() && this.codeBulr()){
               // console.log(11111111)
                let obj={//
                    phone:this.phone,
                    upwd:this.upwd,
                    nickname:this.nickname
                };
                //
                reg(obj).then(res=>{
                    if(res!=0){//
                        //localStroge
                        localStorage.setItem("uid",res.uid);
                        let uid = localStorage.getItem("uid");
                        //uname
                        this.setUid(uid)
                         //
                         this.$router.push("/person");
                    }
                })
            }
        }
         
    }
}
</script>
<style>
/*  */
 .Reg .van-nav-bar__title{
     color:#743481 !important;
     font-size:24px !important;
    padding:15px 0;
 }
 /*  */
 .Reg .van-nav-bar__arrow{
     font-size:24px !important;
     color:#743481 !important;
 }
.Reg .logo{
     text-align:center;
 }
 /* logo */
 .Reg .logo>img{
   width:200px;
   height:200px;
 }
 /*  */
.Reg .Reg-info #nickname,.Reg-info #phone{
     width:70% !important;
     margin:0 auto;
     font-size:18px;
 }
  .Reg .Reg-info .van-cell{
    border-bottom:1px solid #743481;
 }


 /*  */
 /*  */
.Reg  #password{
     width:70%;
     position:relative;;
     margin:0 auto;
 }
 .Reg #password .van-cell{
      margin:0;
      /* width:100% !important; */
  }
 .Reg #password .position{
      position:absolute;
      top:calc(50% - 8px);
      right:0;
  }
  /*  */
  /*  */
.Reg #authCode{
    width:70%;
    position:relative;;
    margin:0 auto;
  }
.Reg #canvas{
      position:absolute;
      background-color:darkgray !important;
      top:9px;
      right:0px;
  }
  /*  input */
    .Reg #authCode .van-field__control{
        height:24PX;
    }
   .Reg #authCode .van-cell {
        margin:0;
 }
/*  */
/*  */
 .Reg #NextFather{
   position: relative;;
   height:100px;
}
.Reg  #Next{
     border:0;
     width:60%;
      /* position:relative; */
     position: absolute;;
     /* margin:0 auto; */
     left:calc(50% - 115px);
     letter-spacing:3px;
     margin-top:40px;
     background-color:#743481;
 }
 /*  */
/*  */
 .Reg .van-checkbox__icon--checked .van-icon{
     background-color:#743481 !important;
     border-color:#743481 !important;
 }
 .Reg #isAgreen{
     display:flex;
     justify-content:center;
     align-items:center;
 }
 .Reg .van-checkbox__icon{
     margin-right:10px;
 }
 
</style>
