import Vue from 'vue'
import VueRouter from 'vue-router'
import store from "../store"
import Priv from '../components/Priv.vue';//
import Reg from '../components/Reg.vue';//
import Login from '../components/Login.vue';//
import Person from '../pages/Person.vue';//
import Read from '../pages/Read.vue';//
import Discover from '../pages/Discover.vue';//
import Home from '../pages/Home.vue';//
import Cloudbook from '../pages/Cloudbook.vue';//
import Feedback from '../pages/Feedback.vue';//
import Friend from '../pages/Friend.vue';//
import MyColl from '../pages/MyColl.vue';//
import MyNote from '../pages/MyNote.vue';//
import MyCreation from '../pages/MyCreation.vue';//
import SET from '../pages/Set.vue';//
import Search from '../pages/Search.vue';//
import BookDetail from '../pages/BookDetail.vue';//
import Excerpt from '../pages/Excerpt.vue';//
import Update from '../pages/Update.vue';//
import SearchList from '../pages/SearchList.vue';//
import Allreview from '../pages/Allreview.vue';//
import Write from '../pages/Write.vue';//
import Viewpoint from '../pages/Viewpoint.vue';//
import SelectedSeacrch from '../pages/SelectedSeacrch.vue';//
import readPage from '../pages/readPage.vue';//
import accountInfo from '../pages/accountInfo';//
import setUpwd from '../pages/setUpwd';//

Vue.use(VueRouter)
 const routes = [
  { path:"/person",component:Person},//
  { path:"/set",component:SET},//
  { path: '/Priv',component: Priv},//
  { path:"/login",component:Login},//
  {path:"/reg",component:Reg},//
  {path:"/index",component:Home},//index
  {path:"/",component:Home},//
  {path:"/discover",component:Discover},//
  {path:"/read",component:Read},//
  {path:"/cloudBook",component:Cloudbook,meta:{isLogin:true}},//
  {path:"/feedback",component:Feedback,meta:{isLogin:true}},//
  {path:"/myColl",component:MyColl,meta:{isLogin:true}},//
  {path:"/myNote",component:MyNote,meta:{isLogin:true}},//
  {path:"/myCreation",component:MyCreation,meta:{isLogin:true}},//
  {path:"/friend",component:Friend,meta:{isLogin:true}},//
  {path:"/search",component:Search},//
  {path:"/bookdetail/:bid",component:BookDetail},//
  {path:"/excerpt/:vid",component:Excerpt},//
  {path:"/update",component:Update},//
  {path:"/searchlist/:bookname",component:SearchList},//
  {path:"/allreview/:bid",component:Allreview},//
  {path:"/write/:bid",component:Write},//
  {path:"/viewpoint",component:Viewpoint},//
  {path:"/selectedseacrch",component:SelectedSeacrch},//
  {path:"/readPage/:bid",component:readPage},//
  {path:"/accountinfo",component:accountInfo},//
  {path:"/setupwd",component:setUpwd},//
  // {
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../pages/About.vue')
  // }
]

const router = new VueRouter({
  routes
})
//
router.beforeEach((to,form,next)=>{
 
  if(to.meta.isLogin){//

     if(!store.state.uid){//
         router.push("/login?redirect="+to.fullPath)
     }else{
       next()
     }
  }else{
    next()
  }
})
export default router
